import {
  TextField,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Typography,
} from '@mui/material';


export default function AddDialog({
  isDeleteOpenModal,
  handleDeleteCloseModal,
  handleAddToy,
}) {
  return (
    <Dialog
      fullWidth
      maxWidth={'md'}
      component="form"
      noValidate
      autoComplete="off"
      open={isDeleteOpenModal}
      onClose={handleDeleteCloseModal}
    >
      <DialogTitle>Eliminar Juguete</DialogTitle>
      <DialogContent>
        <Typography variant='subtitle2' >Estas Seguro?</Typography>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleDeleteCloseModal} variant="outlined">
          Cancelar
        </Button>
        <Button onClick={handleAddToy} variant="contained" disableElevation>
          Eliminar
        </Button>
      </DialogActions>
    </Dialog>
  );
}
