import {
  TextField,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@mui/material';


export default function AddDialog({
  isOpenModal,
  handleCloseModal,
  handleAddToy,
  handleChange,
  toy
}) {
  return (
    <Dialog
      fullWidth
      maxWidth={'md'}
      component="form"
      noValidate
      autoComplete="off"
      open={isOpenModal}
      onClose={handleCloseModal}
    >
      <DialogTitle>Agregar Juguete</DialogTitle>
      <DialogContent>
        <TextField
          autoFocus
          margin="normal"
          label={'Nombre'}
          type="text"
          name='name'
          variant="standard"
          fullWidth
          onChange={handleChange}
          value={toy.name}
        />
        <TextField
          autoFocus
          margin="normal"
          label={'Precio'}
          type="text"
          name='price'
          variant="standard"
          fullWidth
          onChange={handleChange}
          value={toy.price}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleCloseModal} variant="outlined">
          Cancelar
        </Button>
        <Button onClick={handleAddToy} variant="contained" disableElevation>
          Guardar
        </Button>
      </DialogActions>
    </Dialog>
  );
}
